import React,{useState} from 'react';
import './App.css';
import axios from 'axios';



const App = () => {
	const [globaldata,setGlobaldata] = useState({});
	const [data,setData] = useState([]);
	const [cases,setCases] = useState();
	const [todaycases,setTodaycases] = useState();
	const [deaths,setDeaths] = useState();
	const [todaydeaths,setTodaydeaths] = useState();
	const [recovered,setRecovered] = useState();
	const [active,setActive] = useState();
	const [critical,setCritical] = useState();

	axios.get('https://coronavirus-19-api.herokuapp.com/all')
	.then(res =>{
		setGlobaldata(res.data);
	})
	.catch(err =>{
		console.log(err);
	})
	
	 axios.get('https://coronavirus-19-api.herokuapp.com/countries')
	.then(res =>{
		setData(res.data);
	})
	.catch(err =>{
		console.log(err);
	})

	const getvalue = () =>{
		var e = document.getElementById('demo');
		var result = e.options[e.selectedIndex].text;
		for(var x of data){
			if(x.country === result){
				setCases(x.cases)
				setTodaycases(x.todayCases);
				setDeaths(x.deaths);
				setTodaydeaths(x.todayDeaths);
				setRecovered(x.recovered);
				setActive(x.active);
				setCritical(x.critical);
			}
		}

	}

	return (
		<div className='main' align='center'>
		<div className='global'>
			<h2>Global Coronavirus Information</h2>
			<h3>Cases: <span className='globalC'> {globaldata.cases} </span> </h3>
			<h3>Deaths: <span className='globalD'> {globaldata.deaths} </span></h3>
			<h3>Recovered: <span className='globalR'> {globaldata.recovered} </span> </h3>
		</div>
		<div className='countries'>
			<select id='demo'  defaultValue={'DEFAULT'} onChange={getvalue}>
			<option value='DEFAULT' disabled>Select a Country </option>
			{data.map(item => <option id="op" key={item.country} value={item.country}>{item.country}</option>)}
			</select>
			<h3>Cases: {cases}</h3>
			<h3>Today Cases: {todaycases}</h3>
			<h3>Deaths: {deaths}</h3>
			<h3>Today Deaths: {todaydeaths}</h3>
			<h3>Recovered: {recovered}</h3>
			<h3>Active: {active}</h3>
			<h3>Critical: {critical}</h3>
		</div>
		</div>
	)
}
export default App;
